import logo from './logo.svg';
import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';
import { Button , Container, Form, Nav, Navbar, Offcanvas} from 'react-bootstrap';
import {Routes, Route, Link, Navigate} from 'react-router-dom'
import Deffuser from './page/Deffuser';
import Event from './page/Event';
import Join from './page/Join';
import Kindle from './page/Kindle';
import Summer from './page/Summer';
import Winter from './page/Winter';
import Home from './page/Home';
import Footer from './page/Footer';
import Ev from './page/Ev';
import data from './data.js';
import Detail from './page/Detail.js';
import {useState} from 'react';

function App() {
  const [query, setQuery] = useState('');
  const [search, setSearch] = useState('');

  const searchChange = (e) => {
    // console.log("aa")
    // console.log(e.target.value)
    setQuery(e.target.value);
  }

  const searchKey = (e) => {
    // console.log("bbb")
    if(e.key === "Enter") {
      // console.log("bbb")

      setSearch(query);

    }
  }

  const searchButton = (e) => {
    // console.log("ccc")
    e.preventDefault();
    setSearch(query);
  }

// const filterData = data.filter((item) => {
//   return (
//     item.title.includes(search)
//   )
// })

const remove = (str) => {
  return str.split(' ').join('')
}

  const filterData = data.filter((item) => {
    return (
     remove(item.title).includes(remove(search))
    )
  })


  let pathImg = ["https://www.urbanbrush.net/web/wp-content/uploads/edd/2018/07/web-20180703100737553535.png", 

  "https://m.bweye.co.kr/data/cheditor4/1712/0aaef666b4b906365bb62f3fe6c1de7d_20171212154232_qayelxta.jpg",

  "https://img.danawa.com/new/cmpart/promotion/230411_membership/images/event_PC_title.jpg" 
  ]


  return (
    <div className="App">
      {['sm'].map((expand) => (
        <Navbar key={expand} expand={expand} className="bg-body-tertiary mb-3" >
          <Container fluid>
            <Navbar.Brand as = {Link} to ="/">MBC SHOP</Navbar.Brand>
            <Navbar.Toggle aria-controls={`offcanvasNavbar-expand-${expand}`} />
            <Navbar.Offcanvas
              id={`offcanvasNavbar-expand-${expand}`}
              aria-labelledby={`offcanvasNavbarLabel-expand-${expand}`}
              placement="end"
            >
              <Offcanvas.Header closeButton>
                <Offcanvas.Title id={`offcanvasNavbarLabel-expand-${expand}`}>
                MBC SHOP
                </Offcanvas.Title>
              </Offcanvas.Header>
              <Offcanvas.Body>
                <Nav className="justify-content-end flex-grow-1 pe-3">
                  <Nav.Link as = {Link} to ="/deffuser">디퓨저</Nav.Link>
                  <Nav.Link as = {Link} to ="/kindle">캔들</Nav.Link>
                  <Nav.Link as = {Link} to ="/event">이벤트</Nav.Link>
                </Nav>
                <Form className="d-flex" onSubmit = {searchButton}>
                  <Form.Control
                    type="search"
                    placeholder="Search"
                    className="me-2"
                    aria-label="Search" onChange = {searchChange} onKeyDown = {searchKey}
                  />
                  <Button variant="outline-success" onClick = {searchButton} >Search</Button>
                </Form>
              </Offcanvas.Body>
            </Navbar.Offcanvas>
          </Container>
        </Navbar>
      ))}

      <Routes>
          <Route path = "/" element = {<Home filterData = {filterData}/>}></Route>
          <Route path = "/kindle" element = {<Kindle />}></Route>
          <Route path = "/deffuser" element = {<Deffuser />}></Route>
          <Route path="/event" element={ <Event/> } >  
            <Route path="summer" element={<Ev item = {pathImg[0]} />  } />
            <Route path="winter" element={<Ev  item = {pathImg[1]}/> } />
            <Route path = "join" element = {<Ev item = {pathImg[2]} />}></Route>
            <Route path = "/event" element = {<Navigate to = "summer" />} />
          </Route>
          <Route path = "/product/:id" element = {<Detail data = {data} />} />
       </Routes>
       <Footer />
    </div>
  );
}

export default App;
