import React from 'react'
import data from '../data';
import Card from '../Card';


const Kindle = () => {
  
  const items = data.filter((item) => {
    return item.list === "k"
  })
  // console.log(items)
  return (
    <div >
      <img className="banner" src = {`${process.env.PUBLIC_URL}/img/doterra2.jpg`}/>
      <p className='title'>캔들상품</p>
      <div className='contents'>
      {
        items.map((item) => {
          return (
            <Card item = {item}></Card>
          )
        })
      }
      </div>
      
    </div>
  )
}

export default Kindle
