import React from 'react'
import {Container,Row, Col} from 'react-bootstrap';
const Footer = () => {
  return (
    <div className='footer'>
       <Container>
            <Row>
                <Col className='logo'>MBC SHOP</Col>
                <Col>
                    <p>상호 : MBC SHOP </p>
                    <p>주소 : 서울 화곡동 </p>
                    <p>전화번호 : 021231234 </p>
                </Col>
                <Col>
                    <p>평일 09:00 ~ 17:00(토,일,공휴일 휴무)</p> 
                    <p>점심 12:00 ~ 13:00</p>
                    <p>주말 및 공휴일은 1:1 상담을 이용해주세요.</p>
                </Col>
            </Row>
        </Container>
    </div>
  )
}

export default Footer
