import React from 'react'
import { useParams } from 'react-router-dom'
import '../App.css';
import { Button } from 'react-bootstrap';

const Detail = ({data}) => {
    // console.log("data", data)
    const {id} = useParams();
    console.log("id", id);
    const product = data.find((item) => {
        return (
            item.id === parseInt(id)
        )
    })
    console.log(product);

  return (
    <div className='product-detail'>
     <img src={`${process.env.PUBLIC_URL}/img/${product.img}`} />
      <div className='product'>
        <h3>{product.title}</h3>
        <p>{product.content}</p>
        <p>무료배송</p>
        <p>{product.price }원</p>
        <p className='pay'><Button variant="primary">구매하기</Button></p>
        <Button variant="outline-secondary" className='w100'>문의</Button>
        <Button variant="outline-secondary"  className='w100'>찜하기</Button>
        <Button variant="outline-secondary"  className='w100'>장바구니</Button>
      </div>
    </div>
  )
}

export default Detail
