import React from 'react'
import data from '../data.js';
import SwiperImg from '../SwiperImg';

import Card from '../Card.js';


const Home = ({filterData}) => {
  
  return (
    <>
      <SwiperImg />
      <div className='contents'>
        {
          filterData.map((item)=> {
            return(
              <Card item = {item}/>
            )
          })
        }
      </div>
      
    </>
  )
}

export default Home
