import React from 'react'
import data from '../data';
import Card from '../Card';

const Deffuser = () => {
  const items = data.filter(item => item.list === 'd')
  return (
    <div>
       <img className="banner" src = {`${process.env.PUBLIC_URL}/img/doterra1.jpg`}  ></img>
      <p className='title'>디퓨저 상품입니다</p>

      <div className='contents'>

        {

          items.map((item) =>{
            
            return (

            <Card item = {item} ></Card>
        )
        })
        } 
        <img style={{width:"100%", height:"200px", "object-fit":"cover", }} src="https://m.a-scent.com/web/product/tiny/202310/df75a3aeda842e4e1746ce4a18624882.jpg"></img>
      </div>
    </div>
  )
}

export default Deffuser
