import React from 'react'
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';

import './styles.css';
import { Autoplay, Navigation, Pagination} from 'swiper/modules';

const SwiperImg = () => {
  const bg1 = {
    backgroundImage: `url(${process.env.PUBLIC_URL}/img/doterra1.jpg)`,
  }
  const bg2 = {
    backgroundImage: `url(${process.env.PUBLIC_URL}/img/doterra2.jpg)`,
  }
  const bg3 = {
    backgroundImage: `url(${process.env.PUBLIC_URL}/img/doterra3.jpg)`,
  }
  const bg4 = {
    backgroundImage: `url(${process.env.PUBLIC_URL}/img/doterra4.jpg)`,
  }
  return (
    <>
     <Swiper
        spaceBetween={30}
        centeredSlides={true}
        autoplay={{
          delay: 2500,
          disableOnInteraction: false,
        }}
        pagination={{
          clickable: true,
        }}
        navigation={true}
        modules={[Autoplay, Pagination, Navigation]}
        className="mySwiper"
      >
        <SwiperSlide style={bg1}><p>반복되는 일상 속<br/> 감각을 깨우는 향기</p></SwiperSlide>
        <SwiperSlide style={bg2}><p>어느덧 편해지는 순간<br/> 모먼트 디퓨저</p></SwiperSlide>
        <SwiperSlide style={bg3}><p>고급스러움을 더하는<br/> 클래식한 디자인</p></SwiperSlide>
        <SwiperSlide style={bg4}><p>누구나 쉽게 누리는<br/> 나만의 향기 라이프</p></SwiperSlide>
        
      </Swiper>
    </>
  )
}

export default SwiperImg
