import React , {useState, useEffect } from 'react';
import Nav from 'react-bootstrap/Nav';
import { Link, Outlet, useLocation, useNavigate } from 'react-router-dom';


const Event = () => {
  const location = useLocation();
  const navigate = useNavigate();
  const [activeKey, setActiveKey] = useState('link0');
  
  useEffect(() => {
    if (location.pathname === '/event') {
      navigate('/event/summer');
    } else if (location.pathname.includes('winter')) {
      setActiveKey('link1');
    } else if (location.pathname.includes('join')) {
      setActiveKey('link2');
    } else {
      setActiveKey('link0');
    }
  }, [location, navigate]);


  return (
    <div>
      <img className='banner' src="https://cdn.pixabay.com/photo/2017/07/21/23/57/concert-2527495_1280.jpg"></img>
      <p className='title'>이벤트</p>
      
      <Nav  variant="tabs" activeKey={activeKey} onSelect={(selectedKey) => setActiveKey(selectedKey)}>
      <Nav.Item>
        <Nav.Link as = {Link} to="summer" eventKey = "link0"   >여름이벤트</Nav.Link>
      </Nav.Item>
      <Nav.Item>
        <Nav.Link  as = {Link} to="winter"  eventKey = "link1"  >겨울이벤트</Nav.Link>
      </Nav.Item>
      <Nav.Item>
        <Nav.Link  as = {Link} to="join" eventKey = "link2"  >회원가입이벤트</Nav.Link>
      </Nav.Item>
    </Nav>
    <Outlet></Outlet>
    
    </div>
  )
}

export default Event
